.preloader {
  background-color: #2b2c31;
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  transition: 0.3s; }
  .preloader-inner {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100px;
    height: 100px;
    margin: -50px 0 0 -50px;
    -webkit-transform: scale(1);
    transform: scale(1);
    transition: 0.4s; }
    .preloader-inner:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #fff;
      border-radius: 50%;
      -webkit-animation: 1s linear infinite pulse;
              animation: 1s linear infinite pulse; }
  .preloader__first .preloader-inner {
    -webkit-transform: scale(0);
    transform: scale(0); }
  .preloader__loading {
    opacity: 1;
    visibility: visible; }
  .preloader__full {
    transition-delay: 0.4s;
    background-color: #fff; }
    .preloader__full .preloader-inner {
      -webkit-transform: scale(25);
      transform: scale(25); }

@-webkit-keyframes pulse {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); }
  25% {
    -webkit-transform: scale(1.4);
    transform: scale(1.4); }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

@keyframes pulse {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1); }
  25% {
    -webkit-transform: scale(1.4);
    transform: scale(1.4); }
  50% {
    -webkit-transform: scale(1);
    transform: scale(1); } }

body.custom-cursor {
  cursor: none; }
  body.custom-cursor *, body.custom-cursor *:hover {
    cursor: none; }

.cursor {
  position: fixed;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 50%;
  background-color: #fff;
  z-index: 2;
  pointer-events: none;
  mix-blend-mode: difference;
  transition: all 0.1s; }
  .cursor__mini {
    width: 5px;
    height: 5px;
    transition: all 0s; }
  .cursor__state {
    width: 30px;
    height: 30px;
    will-change: left, top;
    transition: left 0.1s, top 0.1s, -webkit-transform 0.6s;
    transition: left 0.1s, top 0.1s, transform 0.6s;
    transition: left 0.1s, top 0.1s, transform 0.6s, -webkit-transform 0.6s; }
    .cursor__state__click {
      -webkit-transform: translate(-50%, -50%) scale(2);
              transform: translate(-50%, -50%) scale(2); }
    .cursor__state__link {
      -webkit-transform: translate(-50%, -50%) scale(1.5);
              transform: translate(-50%, -50%) scale(1.5); }

.blob {
  overflow: hidden;
  transition: all 2s;
  transition-timing-function: linear;
  background: #2b2c31;
  border-radius: 70% 30% 30% 70% / 60% 40% 60% 40%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover; }

